* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: gray;
  font-size: 1.5rem;
  width: 100%;
  padding: 1rem;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  object-fit: cover;
  padding: 1rem;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 17rem;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 1rem;
}